<template>
    <div class="easy-entry">
        <div class="public-detail">
            <div class="important-info">
                <Row>
                    <Col span="4">
                        <div class="name">货运要求</div>
                    </Col>
                    <Col span="20">
                        <Button type="primary" @click="save('formValidate')">保存</Button>
                    </Col>
                </Row>
            </div>
        </div>
        <div class="public-table-content public-input">
            <div class="l-content">
                <div class="table-data">
                    <ul class="transit">
                        <p class="table-title">基本信息</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="运输类型：" prop="name">
                                <Select
                                    v-model="transitModal.name"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="运输类型：" prop="name">
                                <Select
                                    v-model="transitModal.name"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="中转地：">
                                <Select
                                    v-model="transitModal.transit"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="货物类型：" prop="type">
                                <Select v-model="transitModal.type">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">服务参数</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="运输类型：">
                                <RadioGroup v-model="transitModal.radio1">
                                    <Radio label="male">集装箱</Radio>
                                    <Radio label="female">件杂货</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱来源：">
                                <RadioGroup v-model="transitModal.radio2">
                                    <Radio label="male">自备</Radio>
                                    <Radio label="female">租箱</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱尺寸：">
                                <RadioGroup v-model="transitModal.radio3">
                                    <Radio label="male">20</Radio>
                                    <Radio label="female">40</Radio>
                                </RadioGroup>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">运输要求</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="主要运输方式：">
                                <RadioGroup v-model="transitModal.radio1">
                                    <Radio label="male">集装箱</Radio>
                                    <Radio label="female">件杂货</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="物流类型：">
                                <RadioGroup v-model="transitModal.radio2">
                                    <Radio label="male">自备</Radio>
                                    <Radio label="female">租箱</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="提箱类型：">
                                <RadioGroup v-model="transitModal.radio3">
                                    <Radio label="male">20</Radio>
                                    <Radio label="female">40</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱数量：">
                                <Input v-model="transitModal.type" class="input-css"></Input>
                            </FormItem>
                            <FormItem label="货物适应温度：">
                                <Select v-model="transitModal.type">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                            <FormItem label="报关地要求：">
                                <Input v-model="transitModal.type" class="input-css"></Input>
                            </FormItem>
                            <FormItem label="最迟起运时间：">
                                <DatePicker type="date" style="width: 190px"></DatePicker>
                            </FormItem>
                            <FormItem label="最晚到达时间：">
                                <DatePicker type="date" style="width: 190px"></DatePicker>
                            </FormItem>
                            <FormItem label="最低时长要求：">
                                <Select v-model="transitModal.type">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                            <FormItem label="最高价格要求：">
                                <Select v-model="transitModal.type">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">服务参数</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="运输类型：">
                                <RadioGroup v-model="transitModal.radio1">
                                    <Radio label="male">集装箱</Radio>
                                    <Radio label="female">件杂货</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱来源：">
                                <RadioGroup v-model="transitModal.radio2">
                                    <Radio label="male">自备</Radio>
                                    <Radio label="female">租箱</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱尺寸：">
                                <RadioGroup v-model="transitModal.radio3">
                                    <Radio label="male">20</Radio>
                                    <Radio label="female">40</Radio>
                                </RadioGroup>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">运输要求</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="主要运输方式：">
                                <RadioGroup v-model="transitModal.radio1">
                                    <Radio label="male">集装箱</Radio>
                                    <Radio label="female">件杂货</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="物流类型：">
                                <RadioGroup v-model="transitModal.radio2">
                                    <Radio label="male">自备</Radio>
                                    <Radio label="female">租箱</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="提箱类型：">
                                <RadioGroup v-model="transitModal.radio3">
                                    <Radio label="male">20</Radio>
                                    <Radio label="female">40</Radio>
                                </RadioGroup>
                            </FormItem>
                            <FormItem label="集装箱数量：" prop="type">
                                <input v-model="transitModal.type"></input>
                            </FormItem>
                            <FormItem label="货物适应温度：" prop="type">
                                <Select v-model="transitModal.type" style="width:200px">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">货运要求备注</p>
                        <Input v-model="transitModal.textarea" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入货运要求备注，500字以内！"></Input>
                    </ul>
                </div>
            </div>
        </div>
        <adt-reconfirm-modal :showModal="isTransitModal" @on-visible-change="visibleChange" :message="transitText"></adt-reconfirm-modal>
    </div>
</template>

<script>
export default {
    name: 'ConfirmFreightRequest',
    data() {
        return {
            transitModal: {
                name: '',
                type: '',
                transit: '',
                age: '',
                textarea: '',
                radio1: '',
                radio2: '',
                radio3: ''
            },
            transitText: '',
            isTransitModal: false,
            ruleValidate: {
                name: [
                    { required: true, message: 'The name cannot be empty', trigger: 'change' }
                ],
                type: [
                    { required: true, message: 'The name cannot be empty', trigger: 'change' }
                ]
            },
            typeList: [
                {value: '箱', label: '箱'},
                {value: '盒子', label: '盒子'}
            ],
            transitLoading: false,
            transitData: [],
            list: ['加拿大', '北级', '北京', '加州','哈尔滨', '邯郸', '韩国'],
            transitSelect: [],
            checkList: []
        }
    },
    mounted() {
        this.transitText = `中转地过多会导致物流方案的费用过高，<br>时效过长，如需添加请<a class="modal-a" href="">联系客服</a>`
    },
    methods: {
        visibleChange(changeStatus) {
            this.isTransitModal = changeStatus
        },
        save(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            })
        },
        onChangeClick(val) {
            if(this.transitSelect.length >= 5) {
                this.isTransitModal = true
                return
            }
            this.transitSelect.push(val.value)
        },
        transitRemote (val) {
            if (val !== '') {
                this.transitLoading = true
                setTimeout(() => {
                    this.transitLoading = false
                    const list = this.list.map(item => {
                        return {
                            value: item,
                            label: item
                        }
                    })
                    this.transitData = list.filter(item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1)
                }, 200)
            } else {
                this.transitData = []
            }
        },
        delTransit (index) {
            this.transitSelect.splice(index, 1)
        }
    }
}
</script>
<style lang="scss" scoped>
.public-detail{
    .ivu-col-span-20{
        display: block;
        text-align: right;
    }
}
// 单选的样式
.ivu-radio-wrapper{
    width: 100px;
}
.ivu-form-item{
    margin-bottom: 0;
}
</style>